import { mergeStyles } from '@fluentui/react';
import { chunk } from 'lodash';
import React, { FC } from 'react';
import { List } from '../components/List';
import { useVocabDialog } from '../userVocab/VocabDialog';
import { config } from './config';
import { WordListItem } from './WordListItem';

const row = mergeStyles({
  display: 'flex',
});

interface Props {
  vocabs: Vocab[];
}

export const WordList: FC<Props> = ({ vocabs }) => {
  const vocabDialog = useVocabDialog();
  return (
    <List<Vocab[]> items={chunk(vocabs, config.itemsPerRow)} getKey={(item) => item.hanzi} renderedWindowsAhead={10}>
      {(item) => (
        <div className={row}>
          {item.map((vocab) => (
            <WordListItem key={vocab.hanzi} vocab={vocab} onClick={vocabDialog.show} />
          ))}
        </div>
      )}
    </List>
  );
};
