import { IconButton, mergeStyles, PrimaryButton, Stack, Text } from '@fluentui/react';
import React, { useState } from 'react';
import { Title } from '../../AppLayout';
import { HanziBox } from '../../components/HanziBox';
import { SizeBox } from '../../components/SizeBox';
import { useQuizRecorder } from '../../QuizRecord';
import { theme } from '../../Theme';
import { VocabDetail } from '../../userVocab/VocabDetail';
import { useRandomVocab } from './useRandomVocab';
import { Writer } from './Writer';

const writerContainerClassName = mergeStyles({
  width: '80%',
  maxWidth: '80vh',
});

const writerClassName = mergeStyles({
  borderStyle: 'solid',
  borderWidth: 1,
  borderColor: theme.palette.neutralLight,
});

export const QuizPage = () => {
  const [vocab, randomize] = useRandomVocab();
  const [current, setCurrent] = useState(0);
  const { listener, peek, reset, isComplete } = useQuizRecorder();
  const next = () => {
    reset();
    const newCurrent = current + 1;
    if (newCurrent === vocab.hanzi.length) {
      setCurrent(0);
      randomize();
    } else {
      setCurrent(newCurrent);
    }
  };
  return (
    <>
      <Title>Recall Hanzi</Title>
      <Stack.Item>
        <Stack horizontal horizontalAlign="center" tokens={{ padding: 8, childrenGap: 8 }}>
          {vocab.hanzi
            .slice(0, current + (isComplete ? 1 : 0))
            .padEnd(vocab.hanzi.length, '？')
            .split('')
            .map((character, index) => (
              <HanziBox key={index} character={character} active={current === index} />
            ))}
        </Stack>
        <Stack horizontalAlign="center" tokens={{ padding: 8 }}>
          <Text variant="medium" block>
            {vocab.pinyin}
          </Text>
          <Text variant="small" block>
            {vocab.translations.join('; ')}
          </Text>
        </Stack>
      </Stack.Item>
      <Stack.Item>
        <Stack horizontalAlign="center">
          <SizeBox className={writerContainerClassName}>
            {({ width }) => (
              <Writer
                key={[width, current, vocab.hanzi[current]].join()}
                className={writerClassName}
                showOutline={peek.isPeeking}
                character={vocab.hanzi[current]}
                size={width}
                quiz
                {...listener}
              />
            )}
          </SizeBox>
          <Stack horizontal horizontalAlign="center" tokens={{ padding: 16, childrenGap: 8 }}>
            <Stack.Item>
              <IconButton
                iconProps={{ iconName: 'RedEye' }}
                disabled={isComplete}
                onMouseDown={peek.startPeeking}
                onMouseLeave={peek.endPeeking}
                onMouseUp={peek.endPeeking}
              />
            </Stack.Item>
            <Stack.Item>
              <PrimaryButton type="primary" onClick={next} disabled={!isComplete}>
                Next Character
              </PrimaryButton>
            </Stack.Item>
          </Stack>
        </Stack>
      </Stack.Item>
      {isComplete && current === vocab.hanzi.length - 1 && (
        <Stack.Item>
          <VocabDetail vocab={vocab} />
        </Stack.Item>
      )}
    </>
  );
};
