import { QuizData } from '../../QuizRecord';
import { StatisticPerQuiz, StatisticPerCharacter, CharacterSummary } from './interfaces';
import { lastItem, sumBy } from '../../helpers/math';

export function calculateStatisticSummary(characterSummaries: CharacterSummary[]) {
  return {
    quizCount: characterSummaries.map((summary) => summary.statistic.quizCount).reduce((y, x) => y + x, 0),
    characterCount: characterSummaries.length,
    characterSummaries,
    familiarityDistribution: calculateFamiliarityDistribution(characterSummaries)
  }
}

function calculateFamiliarityDistribution(characterSummaries: CharacterSummary[]) {
  let alien = 0;
  let forgotten = 0;
  let familiar = 0;
  let remember = 0;

  characterSummaries.forEach((summary) => {
    const accuracy = summary.statistic.accuracyAverage;
    if (accuracy < 0.5) {
      alien++;
    } else if (accuracy < 0.75) {
      forgotten++;
    } else if (accuracy < 0.90) {
      familiar++;
    } else {
      remember++;
    }
  });

  return { alien, forgotten, familiar, remember };
}

export function reduceStatisticPerCharacter(statisticPerCharacter: StatisticPerCharacter, statisticsPerQuiz: StatisticPerQuiz): StatisticPerCharacter {
  let {
    quizCount,
    mistakesAveragePerQuiz,
    accuracyAverage,
    peekCountAveragePerQuiz,
    peekTimeAveragePerQuiz,
    timeTakenAveragePerQuiz,
    perfectQuizCount,
  } = statisticPerCharacter;

  return {
    quizCount: quizCount + 1,
    mistakesAveragePerQuiz: appendAverage(mistakesAveragePerQuiz, quizCount, statisticsPerQuiz.mistakeCount),
    accuracyAverage: appendAverage(accuracyAverage, quizCount, statisticsPerQuiz.accuracy),
    peekCountAveragePerQuiz: appendAverage(peekCountAveragePerQuiz, quizCount, statisticsPerQuiz.peekCount),
    peekTimeAveragePerQuiz: appendAverage(peekTimeAveragePerQuiz, quizCount, statisticsPerQuiz.peekTime),
    timeTakenAveragePerQuiz: appendAverage(timeTakenAveragePerQuiz, quizCount, statisticsPerQuiz.timeTaken),
    perfectQuizCount: perfectQuizCount + (isPerfect(statisticsPerQuiz) ? 1 : 0),
  };
}

function appendAverage(prevValue: number, prevCount: number, newValue: number) {
  return (prevValue * prevCount + newValue) / (prevCount + 1);
}

export function calculateStatisticPerCharacter(statisticsPerQuiz: StatisticPerQuiz[]): StatisticPerCharacter {
  const sumMistakeCount = sumBy(statisticsPerQuiz, 'mistakeCount');
  const sumPeekCount = sumBy(statisticsPerQuiz, 'peekCount');
  const sumPeekTime = sumBy(statisticsPerQuiz, 'peekTime');
  const sumTimeTaken = sumBy(statisticsPerQuiz, 'timeTaken');
  const sumAccuracy = sumBy(statisticsPerQuiz, 'accuracy');

  const perfectQuizCount = statisticsPerQuiz
    .filter((quizStatistic) => quizStatistic.mistakeCount === 0 && quizStatistic.peekCount === 0)
    .length;
  const quizCount: number = statisticsPerQuiz.length;

  return {
    quizCount,
    mistakesAveragePerQuiz: sumMistakeCount / quizCount,
    accuracyAverage: sumAccuracy / quizCount,
    peekCountAveragePerQuiz: sumPeekCount / quizCount,
    peekTimeAveragePerQuiz: sumPeekTime / quizCount,
    timeTakenAveragePerQuiz: sumTimeTaken / quizCount,
    perfectQuizCount
  };
}

function isPerfect(statisticsPerQuiz: StatisticPerQuiz) {
  return statisticsPerQuiz.mistakeCount === 0 && statisticsPerQuiz.peekCount === 0;
}

export function calculateStatisticPerRecord(record: QuizData) {
  const timeStarted = Math.min(
    lastItem(record.correctStrokes).timestamp.toMillis(),
    lastItem(record.mistakes)?.timestamp.toMillis() || Infinity,
    lastItem(record.peeks)?.timestamp.toMillis() || Infinity,
  );

  const accuracyFactors =
    record.mistakes.length +
    record.correctStrokes.length +
    record.peeks.length * record.correctStrokes.length / 2;

  return {
    timestamp: record.timestamp,
    accuracy: record.correctStrokes.length / accuracyFactors,
    timeTaken: record.timestamp.toMillis() - timeStarted,
    mistakeCount: record.mistakes.length,
    peekCount: record.peeks.length,
    peekTime: sumBy(record.peeks, 'peekTime')
  };
}
