import { IButtonStyles, IconButton, initializeIcons, IStackStyles, Stack, StackItem, Text } from '@fluentui/react';
import React, { FC } from 'react';
import { createContextualPortal } from 'react-maid';
import { Link } from './components/Link';
import { theme } from './Theme';

initializeIcons();

const containerStyles: IStackStyles = {
  root: {
    height: '100vh',
    backgroundColor: theme.palette.white,
  },
};

const mainStyles: IStackStyles = {
  root: {
    flex: 1,
    overflow: 'auto',
  },
};

const titleContainerStyles: IStackStyles = {
  root: {
    position: 'sticky',
    top: 0,
    zIndex: 1,
    backgroundColor: theme.palette.neutralLighterAlt,
    boxShadow: theme.effects.elevation4,
  },
};

const menuStyles: IStackStyles = {
  root: {
    position: 'sticky',
    bottom: 0,
    zIndex: 1,
    backgroundColor: theme.palette.neutralLighterAlt,
    boxShadow: theme.effects.elevation8,
  },
};

const menuIconStyles: IButtonStyles = {
  root: {
    width: 32,
    height: 32,
  },
};

const [TitleProvider, TitlePlaceholder, TitleGate] = createContextualPortal('Title');
export const Title: FC = ({ children }) => {
  return (
    <TitleGate>
      <Text variant="xLarge">{children}</Text>
    </TitleGate>
  );
};

export const AppLayout: FC = ({ children }) => {
  return (
    <TitleProvider>
      <Stack disableShrink styles={containerStyles}>
        <Stack horizontal verticalAlign="center" tokens={{ padding: 16 }} styles={titleContainerStyles}>
          <TitlePlaceholder />
        </Stack>
        <Stack grow styles={mainStyles}>
          {children}
        </Stack>
        <Stack
          horizontal
          verticalAlign="center"
          horizontalAlign="space-evenly"
          tokens={{ padding: 16, childrenGap: 16 }}
          styles={menuStyles}
        >
          <StackItem grow>
            <Link to="/">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: 'Handwriting' }} />
            </Link>
          </StackItem>
          <StackItem grow>
            <Link to="/words">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: 'Dictionary' }} />
            </Link>
          </StackItem>
          <StackItem grow>
            <Link to="/hsk">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: 'TestStep' }} />
            </Link>
          </StackItem>
          <StackItem grow>
            <Link to="/radicals">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: 'Puzzle' }} />
            </Link>
          </StackItem>
          <StackItem grow>
            <Link to="/statistics">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: 'Chart' }} />
            </Link>
          </StackItem>
          <StackItem grow>
            <Link to="/about">
              <IconButton styles={menuIconStyles} iconProps={{ iconName: '6PointStar' }} />
            </Link>
          </StackItem>
        </Stack>
      </Stack>
    </TitleProvider>
  );
};
