import { IStackItemProps, IStackItemStyles, mergeStyleSets, Stack, Text } from '@fluentui/react';
import React, { FC } from 'react';

const infoStyles: IStackItemStyles = {
  root: {
    flexBasis: 0,
  },
};

interface InfoProps extends IStackItemProps {
  title: string;
}

export const Info: FC<InfoProps> = ({ title, children, styles, ...itemProps }) => {
  return (
    <Stack.Item grow styles={mergeStyleSets(styles, infoStyles)} {...itemProps}>
      <Text block variant="small">
        {title}:
      </Text>
      <Text block variant="mediumPlus">
        {children}
      </Text>
    </Stack.Item>
  );
}
