import { IPivotItemProps, Pivot, PivotItem } from '@fluentui/react';
import React, { FC } from 'react';
import { Redirect, useHistory, useParams } from 'react-router';
import { HSK_VOCABS_BY_LEVEL } from '../../data/hsk';
import { Title } from '../AppLayout';
import { WordList } from './WordList';

export const HSKPage: FC = () => {
  const history = useHistory();
  const { level } = useParams<{ level: string }>();
  if (!level) {
    return <Redirect to={`/hsk/1/${HSK_VOCABS_BY_LEVEL[0][0].hanzi}`} />;
  }
  const currentHSK = HSK_VOCABS_BY_LEVEL[+level - 1];

  const handlePivotItemClick = (item?: { props: IPivotItemProps }) => {
    item && history.push(`/hsk/${item.props.itemKey}`);
  };
  return (
    <>
      <Title>HSK {+level}</Title>
      <Pivot onLinkClick={handlePivotItemClick} selectedKey={level}>
        {HSK_VOCABS_BY_LEVEL.map((hsk, level) => (
          <PivotItem key={level} headerText={`HSK ${level + 1}`} itemKey={`${level + 1}`} />
        ))}
      </Pivot>
      <WordList vocabs={currentHSK} />
    </>
  );
};
