import firebase from 'firebase';
import { StrokeData, SummaryData } from 'hanzi-writer';
import { useState } from 'react';
import { useFirebase } from '../firebase';
import { useCurrentUser } from './auth/useCurrentUser';
import { useRecorder, useTimer } from './hooks';
import {
  calculateStatisticPerCharacter,
  calculateStatisticPerRecord,
  reduceStatisticPerCharacter,
} from './pages/StatisticsPage/calculateStatistics';

export interface QuizData {
  timestamp: firebase.firestore.Timestamp;
  peeks: { timestamp: firebase.firestore.Timestamp; peekTime: number }[];
  mistakes: { timestamp: firebase.firestore.Timestamp; strokeIndex: number }[];
  correctStrokes: { timestamp: firebase.firestore.Timestamp; strokeIndex: number }[];
}

export type QuizRecords = QuizData[];

export function useQuizRecorder() {
  const [isPeeking, peeks, startPeeking, endPeeking, clearPeeks] = useTimer();
  const [mistakes, recordMistake, clearMistakes] = useRecorder<number>();
  const [correctStrokes, recordCorrectStroke, clearCorrectStrokes] = useRecorder<number>();
  const [isComplete, setIsComplete] = useState(false);
  const firebase = useFirebase();
  const currentUser = useCurrentUser();

  const onCorrectStroke = (summary: StrokeData) => {
    recordCorrectStroke(summary.strokeNum);
  };
  const onMistake = (summary: StrokeData) => {
    recordMistake(summary.strokeNum);
  };
  const onComplete = async (summary: SummaryData) => {
    setIsComplete(true);
    const record: QuizData = {
      timestamp: firebase.firestore.Timestamp.fromDate(new Date()),
      correctStrokes: correctStrokes.map((stroke: any) => ({
        strokeIndex: stroke.data,
        timestamp: firebase.firestore.Timestamp.fromDate(new Date(stroke.timestamp)),
      })),
      mistakes: mistakes.map((mistake: any) => ({
        strokeIndex: mistake.data,
        timestamp: firebase.firestore.Timestamp.fromDate(new Date(mistake.timestamp)),
      })),
      peeks: peeks.map((peek: any) => ({
        peekTime: peek.data,
        timestamp: firebase.firestore.Timestamp.fromDate(new Date(peek.timestamp)),
      })),
    };

    const characterRef = firebase
      .firestore()
      .collection('users')
      .doc(currentUser.uid)
      .collection('history')
      .doc(summary.character);

    const result = await characterRef.get();
    const data = result.data();
    const history = calculateStatisticPerRecord(record);
    characterRef.collection('writeHistory').add(history);
    characterRef.set({
      character: summary.character,
      statistic: data
        ? reduceStatisticPerCharacter(data.statistic, history)
        : calculateStatisticPerCharacter([history]),
    });
  };

  const reset = () => {
    setIsComplete(false);
    clearPeeks();
    clearMistakes();
    clearCorrectStrokes();
  };

  return {
    listener: { onComplete, onMistake, onCorrectStroke },
    peek: { isPeeking, startPeeking, endPeeking },
    reset,
    isComplete,
  };
}
