import { IStyle, mergeStyles } from '@fluentui/react';
import { PropsWithChildren } from 'react';
import githubSrc from './assets/github.png';
import googleSrc from './assets/google.png';
import linkedinSrc from './assets/linkedin.png';
import twitterSrc from './assets/twitter.png';

const google = { src: googleSrc, alt: 'Google' };
const github = { src: githubSrc, alt: 'GitHub' };
const linkedin = { src: linkedinSrc, alt: 'LinkedIn' };
const twitter = { src: twitterSrc, alt: 'Twitter' };

const icons = { google, github, linkedin, twitter };

const md: IStyle = {
  padding: 4,
  maxWidth: 18,
  maxHeight: 18,
};

const sizes = { md };

const defaultStyles: IImageIconStyles = {
  root: {
    width: '100%',
  },
};

export interface IImageIconStyles {
  root: IStyle;
}

interface ImageIconProps {
  name: keyof typeof icons;
  size?: keyof typeof sizes;
  styles?: IImageIconStyles;
}

export function ImageIcon({ name, size = 'md', styles }: PropsWithChildren<ImageIconProps>) {
  return (
    <img
      alt={icons[name].alt}
      src={icons[name].src}
      className={mergeStyles(defaultStyles.root, sizes[size], styles?.root)}
    />
  );
}
