import jsonData from './radicals.json';

interface Variant {
  character: string;
  strokes: number;
}

export interface Radical {
  id: number;
  variants: Variant[];
};

export const RADICALS: Radical[] = jsonData;
export const STROKE_COUNTS = RADICALS.reduce<Record<string, number>>((result, radical) => {
  radical.variants.forEach((variant) => {
    result[variant.character] = variant.strokes;
  });
  return result;
}, {});
