import { useState } from 'react';

export interface Record<T> {
  timestamp: number;
  data: T;
}

export function useRecorder<T>(initialValue: Record<T>[] = []): [Record<T>[], (record: T) => Record<T>[], VoidFunction] {
  const [records, setRecords] = useState<Record<T>[]>(initialValue);

  const saveRecord = (data: T) => {
    const record: Record<T> = {
      timestamp: new Date().getTime(),
      data,
    }
    const newRecords = [record, ...records];
    setRecords(newRecords);

    return newRecords;
  };

  const reset = () => setRecords([]);

  return [records, saveRecord, reset];
}

export function useTimer(): [boolean, Record<number>[], VoidFunction, VoidFunction, VoidFunction] {
  const [startTime, setStartTime] = useState(0);
  const [records, saveRecord, resetRecord] = useRecorder<number>();
  const isTicking = startTime !== 0;
  const start = () => {
    setStartTime(new Date().getTime());
  };
  const end = () => {
    if (isTicking) {
      saveRecord(new Date().getTime() - startTime);
      setStartTime(0);
    }
  };
  const reset = () => {
    resetRecord();
    setStartTime(0);
  };

  return [isTicking, records, start, end, reset];
}
