import { useEffect, useState } from 'react';
import { useFirebase } from '../firebase';
import { useCurrentUser } from './auth/useCurrentUser';
import { CharacterSummary, StatisticPerQuiz } from './pages/StatisticsPage/interfaces';

export function useHistory() {
  const firebase = useFirebase();
  const currentUser = useCurrentUser();

  const [data, setData] = useState<CharacterSummary[] | undefined>();

  useEffect(() => {
    firebase.firestore()
      .collection('users').doc(currentUser.uid)
      .collection('history')
      .onSnapshot((data) => {
        setData(data.docs.map((x) => x.data() as CharacterSummary));
      });
  }, [firebase, currentUser.uid]);

  return data;
}

export function useHistoryPerCharacter(character: string) {
  const firebase = useFirebase();
  const currentUser = useCurrentUser();

  const [data, setData] = useState<StatisticPerQuiz[] | undefined>();

  useEffect(() => {
    firebase.firestore()
      .collection('users').doc(currentUser.uid)
      .collection('history').doc(character)
      .collection('writeHistory')
      .onSnapshot((data) => {
        setData(data.docs.map((x) => x.data() as StatisticPerQuiz));
      });
  }, [firebase, currentUser.uid, character]);

  return data;
}
