import { DetailsList, IColumn, SelectionMode, DetailsListLayoutMode } from '@fluentui/react';
import React, { FC, useMemo } from 'react';

import { CharacterSummary } from './interfaces';
import { renderPercent } from '../../helpers/formatter';
import { CharacterDetailSummaryButton } from './CharacterDetailSummaryButton';

const SUMMARY_COLUMN: IColumn[] = [
  {
    key: 'character',
    fieldName: 'character',
    name: 'Hanzi',
    data: 'string',
    minWidth: 64,
  },
  {
    key: 'statistic.quizCount',
    fieldName: 'statistic.quizCount',
    name: 'Quiz Count',
    minWidth: 32,
    maxWidth: 72,
    data: 'number',
    onRender: (item) => item.statistic.quizCount,
  },
  {
    key: 'statistic.perfectQuizCount',
    fieldName: 'statistic.perfectQuizCount',
    name: 'Perfect Count',
    minWidth: 32,
    maxWidth: 72,
    data: 'number',
    onRender: (item) => item.statistic.perfectQuizCount,
  },
  {
    key: 'statistic.accuracyAverage',
    fieldName: 'statistic.accuracyAverage',
    name: 'Accuracy',
    minWidth: 32,
    maxWidth: 72,
    data: 'number',
    isSorted: true,
    isSortedDescending: true,
    onRender: (item) => renderPercent(item.statistic.accuracyAverage),
  },
  {
    key: 'action',
    fieldName: 'action',
    name: '',
    minWidth: 32,
    maxWidth: 32,
    onRender: (item) => {
      return <CharacterDetailSummaryButton summary={item} />;
    },
  }
];
interface Props {
  characterSummaries: CharacterSummary[];
}

export const CharacterSummariesTable: FC<Props> = ({ characterSummaries: statistics }) => {
  const sortedStatistics = useMemo(() => statistics.sort((a, b) => {
    return b.statistic.accuracyAverage - a.statistic.accuracyAverage;
  }), [statistics]);
  return (
    <DetailsList
      items={sortedStatistics}
      columns={SUMMARY_COLUMN}
      getKey={getKey}
      selectionMode={SelectionMode.none}
      layoutMode={DetailsListLayoutMode.justified}
      isHeaderVisible={true}
    />
  );
};

function getKey(item: CharacterSummary) {
  return item.character;
}