import React, { FC, HTMLProps, ReactNode, useEffect, useRef, useState } from 'react';

interface Props extends HTMLProps<HTMLDivElement> {
  children: (rect: ClientRect) => ReactNode;
}

export const SizeBox: FC<Props> = ({ children, ...props }) => {
  const [rect, setRect] = useState<ClientRect>();
  const divRef = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const handleResize = () => {
      if (divRef.current) {
        const rect = divRef.current.getBoundingClientRect();
        setRect(rect);
      }
    };

    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return <div ref={divRef} {...props}>{rect && children(rect)}</div>;
};

