import { DefaultButton, IButtonStyles, mergeStyles } from '@fluentui/react';
import React, { FC } from 'react';
import { config } from './config';

const hanziStyle = mergeStyles({ fontSize: config.hanziFontSize });
const pinyinStyle = mergeStyles({ fontSize: config.pinyinFontSize });

const listItemStyles: IButtonStyles = {
  root: {
    width: config.size,
    height: config.size,
    padding: 0,
    border: 0,
    borderRadius: 0,
  },
  flexContainer: {
    flexDirection: 'column',
  },
};

interface Props {
  vocab: Vocab;
  onClick: (vocab: Vocab) => void;
}

export const WordListItem: FC<Props> = ({ vocab, onClick }) => {
  const handleClick = () => {
    onClick(vocab);
  };

  return (
    <DefaultButton onClick={handleClick} styles={listItemStyles}>
      <div className={hanziStyle}>{vocab.hanzi}</div>
      <div className={pinyinStyle}>{vocab.pinyin}</div>
    </DefaultButton>
  );
};
