import { useState } from 'react';
import { PHRASES } from '../../../data/data';
import { avg, randomItem } from '../../helpers/math';
import { useHistory } from '../../useHistory';
import { calculateStatisticSummary } from '../StatisticsPage/calculateStatistics';



export function useRandomVocab(): [Vocab, () => void] {
  const [vocab, setVocab] = useState(() => randomItem(PHRASES));
  const records = useHistory();

  const randomize = () => {
    let nextVocab: Vocab;
    const summary = calculateStatisticSummary(records || []);
    do {
      nextVocab = randomItem(PHRASES);
      const accuracy = avg(
        nextVocab.hanzi.split('').map((character) =>
          summary.characterSummaries.find((summary) => summary.character === character)?.statistic.accuracyAverage || 0
        )
      );
      // make high accuracy text appear less. add 0.1 to make 100% accuracy still able to appear
      if (Math.random() + 0.1 < accuracy) {
        continue;
      }
    } while (nextVocab === vocab);
    setVocab(nextVocab);
  }
  return [vocab, randomize];
}