import React, { FC } from 'react';
import { useToggle } from 'react-maid';

import { TableIconButton } from '../../components/TableIconButton';
import { CharacterDetailSummaryDialog } from './CharacterDetailSummaryDialog';
import { CharacterSummary } from './interfaces';

interface Props {
  summary: CharacterSummary;
}

export const CharacterDetailSummaryButton: FC<Props> = ({ summary }) => {
  const [isOpen, toggleOpen] = useToggle();
  return (
    <>
      <TableIconButton iconProps={{ iconName: 'MiniExpandMirrored' }} onClick={toggleOpen} />
      {isOpen && <CharacterDetailSummaryDialog summary={summary} isOpen={isOpen} onDismiss={toggleOpen} />}
    </>
  );
};
