import { IconButton, IModalProps, IModalStyles, Modal, Spinner, Stack, Text } from '@fluentui/react';
import { FC } from 'react';
import { PHRASES } from '../../../data/data';
import { dictionary } from '../../../data/dictionary';
import { Info } from '../../components/Info';
import { renderDuration, renderPercent } from '../../helpers/formatter';
import { useHistoryPerCharacter } from '../../useHistory';
import { CharacterHistoryDetailList } from './CharacterHistoryDetailList';
import { CharacterSummary } from './interfaces';

const modalStyles: Partial<IModalStyles> = {
  main: {
    display: 'flex',
    flexDirection: 'column',
  },
};

interface Props extends IModalProps {
  onDismiss: () => void;
  summary: CharacterSummary;
}

export const CharacterDetailSummaryDialog: FC<Props> = ({ summary, ...props }) => {
  const vocabs = PHRASES.filter((word) => word.hanzi.includes(summary.character) && word.hanzi.length > 1);
  const perQuizHistories = useHistoryPerCharacter(summary.character);
  return (
    <Modal {...props} styles={modalStyles}>
      <Stack tokens={{ padding: 16, childrenGap: 16 }}>
        <Stack horizontal horizontalAlign="space-between">
          <Stack horizontal verticalAlign="center" tokens={{ childrenGap: 8 }}>
            <Text variant="xLargePlus">{summary.character}</Text>
            <Text variant="smallPlus">({dictionary[summary.character].pinyin})</Text>
          </Stack>
          <Stack.Item>
            <IconButton iconProps={{ iconName: 'Cancel' }} ariaLabel="Close popup modal" onClick={props.onDismiss} />
          </Stack.Item>
        </Stack>
        <Info title="Definition">{dictionary[summary.character].definition}</Info>
        {vocabs.length && <Info title="Used in Words">{vocabs.map((vocab) => vocab.hanzi).join('，')}</Info>}
        <Stack horizontal wrap>
          <Info title="Quiz Taken">{summary.statistic.quizCount}</Info>
          <Info title="Perfect Count">{summary.statistic.perfectQuizCount}</Info>
          <Info title="Avg. Time Taken">{renderDuration(summary.statistic.timeTakenAveragePerQuiz)}</Info>
        </Stack>
        <Stack horizontal wrap>
          <Info title="Avg. Peek Count">{summary.statistic.peekCountAveragePerQuiz}</Info>
          <Info title="Avg. Mistakes">{summary.statistic.mistakesAveragePerQuiz}</Info>
          <Info title="Accuracy">{renderPercent(summary.statistic.accuracyAverage)}</Info>
        </Stack>
        <Stack>
          {!perQuizHistories && <Spinner />}
          {perQuizHistories && <CharacterHistoryDetailList histories={perQuizHistories} />}
        </Stack>
      </Stack>
    </Modal>
  );
};
