import { IStyle, mergeStyles } from '@fluentui/react';
import React, { FC, HTMLProps } from 'react';
import { theme } from '../Theme';

function hanziStyles(active: boolean): IStyle {
  return {
    width: 32,
    height: 32,
    fontSize: 18,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderStyle: 'solid',
    borderWidth: 1,
    borderColor: active ? theme.palette.themePrimary : theme.palette.neutralLight,
  };
}

interface Props extends HTMLProps<HTMLDivElement> {
  character: string;
  active?: boolean;
}

export const HanziBox: FC<Props> = ({ character, active, className, ...props }) => {
  return (
    <div {...props} className={mergeStyles(hanziStyles(!!active), className)}>
      {character}
    </div>
  );
};
