import { Modal, Stack, Text } from '@fluentui/react';
import React, { FC, useState } from 'react';
import { createContextWithHook } from 'react-maid';
import { HSK_VOCABS_BY_LEVEL } from '../../data/hsk';
import { VocabDetail } from './VocabDetail';

interface VocabDialogContext {
  show: (vocab: Vocab) => void;
}

export const [VocabDialog, useVocabDialog] = createContextWithHook<VocabDialogContext>('VocabDialogContext');

export const VocabDialogProvider: FC = ({ children }) => {
  const [isModalOpen, toggleOpenModal] = useState(false);
  const [activeVocab, setActiveVocab] = useState<Vocab>(HSK_VOCABS_BY_LEVEL[0][0]);

  const show = (vocab: Vocab) => {
    toggleOpenModal(true);
    setActiveVocab(vocab);
  };

  const handleDismiss = () => {
    toggleOpenModal(false);
  };
  return (
    <VocabDialog.Provider value={{ show }}>
      {children}
      <Modal isOpen={isModalOpen} onDismiss={handleDismiss} isBlocking={false}>
        <Stack tokens={{ padding: 16, childrenGap: 16 }}>
          <Text block variant="xLargePlus">
            {activeVocab.hanzi}&nbsp;<Text variant="small">({activeVocab.pinyin})</Text>
          </Text>
          <VocabDetail vocab={activeVocab} />
        </Stack>
      </Modal>
    </VocabDialog.Provider>
  );
};
