import { Link, Text } from '@fluentui/react';
import React, { FC, Fragment } from 'react';
import { useVocabDialog } from './VocabDialog';

interface Props {
  vocabs: Vocab[];
}

export const VocabList: FC<Props> = ({ vocabs }) => {
  const vocabDialog = useVocabDialog();
  return (
    <>
      {vocabs.length === 0 ? (
        <Text variant="large">-</Text>
      ) : (
        vocabs.map((vocab, index) => (
          <Fragment key={vocab.hanzi}>
            {index > 0 && '，'}
            <Link onClick={() => vocabDialog.show(vocab)}>{vocab.hanzi}</Link>
          </Fragment>
        ))
      )}
    </>
  );
};
