import { dictionary } from './dictionary';
import displayedHSK from './displayed-hsk.json';
import { HSK_VOCABS_BY_LEVEL } from './hsk';
import otherPhrases from './phrases.json';

export const USER_HSK = displayedHSK.reduce<Dictionary<number[]>>((result, [level, index]) => {
  result[level] = result[level] || [];
  result[level].push(index);
  return result;
}, {});
export const OTHER_PHRASES = otherPhrases;
export const PHRASES = [
  HSK_VOCABS_BY_LEVEL[0],
  HSK_VOCABS_BY_LEVEL[1],
  HSK_VOCABS_BY_LEVEL[2],
  HSK_VOCABS_BY_LEVEL[3],
  displayedHSK.map(([level, index]) => HSK_VOCABS_BY_LEVEL[level][index]),
  OTHER_PHRASES,
]
  .flat()
  .filter((v, i, s) => s.indexOf(v) === i);

export function getWordsByCharacter(character: string) {
  return PHRASES.filter((vocab) => vocab.hanzi.includes(character));
}

export function getWordsContainingCharacter(targetCharacter: string) {
  return PHRASES.filter(
    (vocab) =>
      !vocab.hanzi.includes(targetCharacter) &&
      vocab.hanzi.split('').some((character) => dictionary[character].decomposition.includes(targetCharacter))
  );
}
