import { getBackgroundShade, getColorFromString, IButtonStyles, PrimaryButton, Shade } from '@fluentui/react';
import { IImageIconStyles, ImageIcon } from '../components/ImageIcon';
import { useSignInWithGoogle } from './useSignInWithGoogle';

export function SignInWithGoogleButton() {
  const signIn = useSignInWithGoogle();

  return (
    <PrimaryButton styles={googleButton} onClick={signIn}>
      <ImageIcon name="google" size="md" styles={iconStyles} />
      Sign In with Google
    </PrimaryButton>
  );
}

const iconStyles: IImageIconStyles = {
  root: {
    backgroundColor: '#FFFFFF',
    marginRight: 8,
  },
};
const googleButton: IButtonStyles = {
  root: {
    backgroundColor: '#4285F4',
    borderStyle: 'none',
    color: '#FFFFFF',
    paddingLeft: 3,
  },
  rootHovered: {
    borderStyle: 'none',
    backgroundColor: getBackgroundShade(getColorFromString('#4285F4')!, Shade.Shade2)!.str,
  },
  rootPressed: {
    borderStyle: 'none',
    backgroundColor: getBackgroundShade(getColorFromString('#4285F4')!, Shade.Shade4)!.str,
  },
};
