import React from 'react';
import { Route, Switch } from 'react-router';
import { AppLayout } from './AppLayout';
import { AuthGuard } from './auth/AuthGuard';
import { AboutPage } from './pages/AboutPage';
import { AuthPage } from './pages/AuthPage';
import { HSKPage } from './pages/HSKPage';
import { QuizPage } from './pages/QuizPage';
import { RadicalsPage } from './pages/RadicalsPage';
import { StatisticsPage } from './pages/StatisticsPage';
import { WordsPage } from './pages/WordsPage';

export const App = () => {
  return (
    <AuthGuard component={AuthPage}>
      <AppLayout>
        <Route>
          <Switch>
            <Route path="/" exact={true} component={QuizPage} />
            <Route path="/about" component={AboutPage} />
            <Route path="/statistics" component={StatisticsPage} />
            <Route path="/words/:hanzi?" component={WordsPage} />
            <Route path="/hsk/:level?/:hanzi?" component={HSKPage} />
            <Route path="/radicals" component={RadicalsPage} />
          </Switch>
        </Route>
      </AppLayout>
    </AuthGuard>
  );
};
