import { Stack } from '@fluentui/react';
import React from 'react';
import { Info } from '../../components/Info';
import { Loader } from '../../components/Loader';
import { useHistory } from '../../useHistory';
import { calculateStatisticSummary } from './calculateStatistics';
import { CharacterSummariesTable } from './CharacterSummaryTable';

export const StatisticsPage = () => {
  const data = useHistory();
  if (!data) {
    return <Loader />;
  }

  const summary = calculateStatisticSummary(data);
  return (
    <Stack tokens={{ padding: 16, childrenGap: 16 }}>
      <Stack horizontal>
        <Info title="Quiz Taken">{summary.quizCount}</Info>
        <Info title="Characters Written">{summary.characterCount}</Info>
      </Stack>
      <Stack horizontal>
        <Info title="Alien">{summary.familiarityDistribution.alien}</Info>
        <Info title="Forgot">{summary.familiarityDistribution.forgotten}</Info>
        <Info title="Familiar">{summary.familiarityDistribution.familiar}</Info>
        <Info title="Remember">{summary.familiarityDistribution.remember}</Info>
      </Stack>
      <Stack>
        <CharacterSummariesTable characterSummaries={summary.characterSummaries} />
      </Stack>
    </Stack>
  );
};
