import { ILinkProps, ILinkStyles as IMsLinkStyles, Link as MsLink } from '@fluentui/react';
import React, { FC, MouseEventHandler } from 'react';
import { useHistory } from 'react-router-dom';

interface Props extends ILinkProps {
  to: string;
}
export type ILinkStyles = IMsLinkStyles;

export const Link: FC<Props> = ({ to, children, onClick, ...props }) => {
  const history = useHistory();
  const handleClick: MouseEventHandler<HTMLElement> = (e) => {
    if (onClick) {
      onClick(e);
    }
    history.push(to);
  };

  return (
    <MsLink onClick={handleClick} {...props}>
      {children}
    </MsLink>
  );
};
