import React, { FC } from 'react';
import { DetailsList, IColumn, SelectionMode, DetailsListLayoutMode } from '@fluentui/react';
import { StatisticPerQuiz } from './interfaces';

interface Props {
  histories: StatisticPerQuiz[];
}

const SUMMARY_COLUMN: IColumn[] = [
  {
    key: 'timestamp',
    fieldName: 'timestamp',
    name: 'Date',
    data: 'date',
    minWidth: 48,
    onRender(item) {
      return item.timestamp.toDate().toLocaleDateString();
    },
  },
  {
    key: 'mistakeCount',
    fieldName: 'mistakeCount',
    name: 'Mistakes',
    minWidth: 48,
  },
  {
    key: 'peekCount',
    fieldName: 'peekCount',
    name: 'Peeks',
    minWidth: 48,
  },
  {
    key: 'accuracy',
    fieldName: 'accuracy',
    name: 'Accuracy',
    minWidth: 48,
  }
];

export const CharacterHistoryDetailList: FC<Props> = ({ histories }) => {
  return (
    <DetailsList
      items={histories}
      columns={SUMMARY_COLUMN}
      getKey={getKey}
      selectionMode={SelectionMode.none}
      layoutMode={DetailsListLayoutMode.justified}
      isHeaderVisible={true}
    />
  );
}

function getKey(item: StatisticPerQuiz) {
  return item.timestamp.toDate().toLocaleDateString();
}