import { IListProps, List as MSList } from '@fluentui/react';
import React, { ReactNode } from 'react';

export interface ListItemProps<T> {
  item: T;
}

interface Props<T> extends IListProps {
  items: T[];
  children: (item: T) => ReactNode;
}

export function List<T>({ items, children, ...props }: Props<T>) {
  const handleRenderCell = (item?: T) => item && children(item);
  return <MSList<T> items={[...items]} onRenderCell={handleRenderCell} {...props} usePageCache />;
}
