import { mergeStyles, Stack } from '@fluentui/react';
import React, { FC, useState } from 'react';
import { getWordsByCharacter, getWordsContainingCharacter } from '../../data/data';
import { dictionary } from '../../data/dictionary';
import { STROKE_COUNTS } from '../../data/radicals';
import { HanziBox } from '../components/HanziBox';
import { Info } from '../components/Info';
import { unique } from '../helpers/array';
import { VocabList } from './VocabList';

interface Props {
  vocab: Vocab;
}

export const VocabDetail: FC<Props> = ({ vocab }) => {
  const characters = vocab.hanzi.split('').filter(unique);
  const [character, setCharacter] = useState(characters[0]);

  const otherWordsByCharacter = getWordsByCharacter(character).filter((v) => v.hanzi !== vocab.hanzi);
  const otherWordsContainingCharacter = getWordsContainingCharacter(character).filter((v) => v.hanzi !== vocab.hanzi);

  const decomposition = getDecomposition(character);
  return (
    <>
      <Stack tokens={{ childrenGap: 8 }}>
        <Info title="Definition">{vocab.translations.join('; ')}</Info>

        <Stack horizontal tokens={{ childrenGap: 8 }}>
          {characters.map((c) => (
            <HanziBox key={c} character={c} active={c === character} onClick={() => setCharacter(c)} />
          ))}
        </Stack>
        <Info title="Pinyin">{dictionary[character].pinyin}</Info>
        <Info title="Radical">{dictionary[character].radical}</Info>
        <Info title="Definition">{dictionary[character].definition}</Info>
        <Info title={`Other vocabs using ${character}`}>
          <VocabList vocabs={otherWordsByCharacter} />
        </Info>

        <Info title={`Other vocabs containing ${character}`}>
          <VocabList vocabs={otherWordsContainingCharacter} />
        </Info>
        {decomposition.length > 0 && (
          <Info title="Composition">
            <div>{getDecomposition(character)}</div>
          </Info>
        )}
      </Stack>
    </>
  );
};

const className = mergeStyles({
  paddingLeft: 8,
});

function getDecomposition(character: string) {
  return dictionary[character].decomposition
    .split('')
    .filter((part) => !!dictionary[part])
    .filter(unique)
    .filter((part) => !STROKE_COUNTS[part] || STROKE_COUNTS[part] > 2)
    .map((part) => (
      <>
        {part} ({dictionary[part].pinyin.join(', ')}): {dictionary[part].definition}
        <div className={className}>{getDecomposition(part)}</div>
      </>
    ));
}
