import { IStackItemStyles, Stack, Text } from '@fluentui/react';
import React, { FC } from 'react';
import { dictionary } from '../../../data/dictionary';
import { Radical } from '../../../data/radicals';
import { Info } from '../../components/Info';
import { theme } from '../../Theme';

const rootStyles: IStackItemStyles = {
  root: {
    border: `solid 1px ${theme.palette.neutralLight}`,
    padding: 8,
    margin: '8px 0px'
  },
};

interface Props {
  radical: Radical;
}

export const RadicalCard: FC<Props> = ({ radical }) => {
  const mainVariant = radical.variants[0];
  const otherVariants = radical.variants.slice(1);
  const detail = dictionary[mainVariant.character];
  return (
    <Stack.Item styles={rootStyles}>
      <Stack tokens={{ padding: 8, childrenGap: 16 }}>
        <Stack horizontal horizontalAlign="space-between">
          <Text variant="xLarge">
            {mainVariant.character} ({detail.pinyin.join('; ')})
          </Text>
          <Text>#{radical.id}</Text>
        </Stack>
        <Stack horizontal>
          <Info title="Strokes" grow>
            {mainVariant.strokes}
          </Info>
          {!!otherVariants.length && (
            <Info title="Variants" grow>
              {otherVariants.map((variant) => variant.character).join('，')}
            </Info>
          )}
        </Stack>
        <Info title="Definition">
          {detail.definition}
        </Info>
      </Stack>
    </Stack.Item>
  );
};
